/*  boxwidthpaddingborder
    *html()  */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/*  a */
a {
    // color: #00b894;
    text-decoration: none;
    text-decoration-color: none;
    &:visited,
    &:link {
        color: inherit; // 
        text-decoration: none;
        background-color: none;
    }
}

/* max-width
        width:100% */
img {
    max-width: 100%;
    height: auto;
}

